import React from 'react';
import PropTypes from 'prop-types';

import {
	Navigation, Row, Col, Button,
	Form, Input, Radio, Checkbox, Tooltip,
} from '../../../components';

import { TYPE_LIST, SIZE_LIST } from '../../utils/enum';
import { toString } from '../../utils/propsUtil';

class ButtonPage extends React.Component {
	constructor() {
		super();
		this.state = {
			form: {
				title: 'Hello, Bamboo UI!',
				placement: 'top',
				inject: false,
			},
		};
	}

	getSampleCode = () => {
		const { form } = this.state;

		return `
<Tooltip${toString(form, { placement: 'top' })}>
	<Button>Hover Me!</Button>
</Tooltip>
`.replace(/\t/g, '   ').trim();
	};

	render() {
		const { form } = this.state;

		return (
			<div>
				<h1></h1>
				<p>
					
					<code>inject</code>dom
				</p>
				<p>
					(<code>inject</code><code>onMouseEnter</code><code>onMouseLeave</code>)
				</p>

				<div className="measurement">
					<div className="preview">
						<Tooltip title="Hello, Bamboo UI!">
							
						</Tooltip>
					</div>

					<pre className="code">
						{`
<Tooltip title="Hello, Bamboo UI!">

</Tooltip>
						`.replace(/\t/g, '   ').trim()}
					</pre>
				</div>

				<h2></h2>
				<div className="measurement">
					<div className="preview">
						<Tooltip
							title={form.title}
							placement={form.placement}
							inject={form.inject}
						>
							<Button>Hover Me!</Button>
						</Tooltip>
					</div>
					<div>
						<Form instance={this} path="form">
							<Form.Field name="title" title="Title">
								<Input />
							</Form.Field>
							<Form.Field name="placement" title="placement">
								<Radio value="top">Top (default)</Radio>
								<Radio value="bottom">Bottom</Radio>
								<Radio value="left">Left</Radio>
								<Radio value="right">Right</Radio>
							</Form.Field>
							<Form.Field name="inject" title="Inject">
								<Checkbox>inject</Checkbox>
							</Form.Field>
						</Form>
					</div>

					<pre className="code">
						{this.getSampleCode()}
					</pre>
				</div>
			</div>
		);
	}
}

ButtonPage.propTypes = {
	history: PropTypes.object,
};

export default ButtonPage;



