import React from 'react';
import PropTypes from 'prop-types';

import {
	Navigation, Row, Col, Button, Group,
	Form, Input, Radio, Checkbox, Menu,
} from '../../../components';

import { TYPE_LIST, SIZE_LIST } from '../../utils/enum';
import { toString } from '../../utils/propsUtil';

class GroupPage extends React.Component {
	constructor() {
		super();
		this.state = {
			type: 'primary',
			size: 'md',
			radio: 1,
			transparent: false,
		};
	}

	getSampleCode = () => {
		const { radio, ...form } = this.state;
		const btnStr = toString(form, { type: 'primary', size: 'md' });

		return `
<Group>
   <Button${btnStr}>Button 1</Button>
   <Button${btnStr}>Button 2</Button>
   <Button${btnStr}>Button 3</Button>
</Group>

<Group value={${radio}}>
   <Radio value={1}${btnStr}>Radio 1</Radio>
   <Radio value={2}${btnStr}>Radio 2</Radio>
   <Radio value={3}${btnStr}>Radio 3</Radio>
</Group>

<Group>
   <Input />
   <Button${btnStr}>Go</Button>
</Group>
`.trim();
	};

	render() {
		const { type, transparent, size } = this.state;

		return (
			<div>
				<h1></h1>
				<p>
					
					<code>Button</code><code>Radio</code><code>Input</code>
				</p>
				<p>
					<code>Form.Field</code>
					<code>Group</code><code>Form.Field</code>
				</p>
				<pre>
					{`
<Form instance={this}>
<Form.Field name="radio">
  <Group>
	 <Radio>Radio 1</Radio>
	 <Radio>Radio 2</Radio>
  </Group>
</Form.Field>
</Form>
`.trim()}
				</pre>

				<p>
					* <code>Group</code><code>Radio</code>
					<code>Radio</code><code>Button</code>
					<code>Radio</code><code>Button</code>
				</p>

				<h2></h2>
				<Form instance={this}>
					<div className="measurement">
						<div className="preview">
							<Group>
								{[1, 2, 3].map(val => (
									<Button key={val} type={type} size={size} transparent={transparent}>
										Button {val}
									</Button>
								))}
							</Group>

							<Form.Field name="radio">
								<Group>
									{[1, 2, 3].map(val => (
										<Radio
											key={val}
											type={type}
											size={size}
											value={val}
											transparent={transparent}
										>Radio {val}</Radio>
									))}
								</Group>
							</Form.Field>

							<Group>
								<Input aria-label="input" size={size} />
								<Button type={type} size={size} transparent={transparent}>Go</Button>
							</Group>
						</div>

						<div className="form">
							<Form.Field name="type" title="Type">
								{TYPE_LIST.map(({ name, isDefault }) => (
									<Radio key={name} value={name}>
										{name}
										{isDefault && ' (default)'}
									</Radio>
								))}
							</Form.Field>
							<Form.Field name="size" title="Size">
								{SIZE_LIST.map(({ name, displayName, isDefault }) => (
									<Radio key={name} value={name}>
										{displayName}
										{isDefault && ' (default)'}
									</Radio>
								))}
							</Form.Field>
							<Form.Field name="transparent" title="Transparent">
								<Checkbox>transparent</Checkbox>
							</Form.Field>
						</div>

						<pre className="code">
							{this.getSampleCode()}
						</pre>
					</div>
				</Form>

				<h2></h2>
				<p>
					<code>Group</code>
				</p>
				<p>
					<code>Group</code><code>Group.Item</code>
					<code>Group.Item</code>
				</p>

				<div className="measurement">
					<div className="preview">
						<Group>
							<Group.Item width="100%">
								<Input aria-label="input" />
							</Group.Item>
							<Button>Go</Button>
						</Group>
					</div>

					<pre className="code">
						{`<Group>
<Group.Item width="100%">
  <Input />
</Group.Item>
<Button>Go</Button>
</Group>`}
					</pre>
				</div>
			</div>
		);
	}
}

GroupPage.propTypes = {
	history: PropTypes.object,
};

export default GroupPage;



