import React from 'react';
import PropTypes from 'prop-types';

import {
	Navigation, Row, Col, Button, Group,
	Form, Input, Radio, Checkbox, Menu,
	Modal,
} from '../../../components';

import { TYPE_LIST } from '../../utils/enum';
import { toString } from '../../utils/objectUtil';

class GroupPage extends React.Component {
	constructor() {
		super();
		this.state = {
			visible: false,
			typesVisible: {},
			prefaceVisible: false,
			confirmVisible: false,
			loadingVisible: false,
			loading: false,
		};
	}

	onShowModal = () => {
		this.setState({ visible: true });
	};
	onHideModal = () => {
		this.setState({ visible: false });
	};

	render() {
		const {
			visible, typesVisible, prefaceVisible, confirmVisible,
			loadingVisible, loading,
			lockVisible, lock,
		} = this.state;

		return (
			<div>
				<h1></h1>
				<p>
					
				</p>

				<div className="measurement">
					<div className="preview">
						<Button onClick={this.onShowModal}></Button>
						<Modal visible={visible} onClose={this.onHideModal} size="sm">
							<Modal.Title>Modal</Modal.Title>
							Hello, Bamboo UI!~
						</Modal>
					</div>

					<pre className="code">
						{`
<Modal
title="Modal"
visible={this.state.visible}
onClose={() => { this.setState({ visible: false }); }}
>
Hello, Bamboo UI!~
</Modal>
`.trim()}
					</pre>
				</div>

				<h2></h2>
				<p>
					
					<code>type</code><code>Button</code>
				</p>
				<div className="preview">
					{TYPE_LIST.map(({ name }) => (
						<div className="inline" key={name}>
							<Button
								onClick={() => {
									this.setState({
										typesVisible: {
											...typesVisible,
											[name]: true,
										},
									});
								}}
								type={name}
							></Button>{' '}
							<Modal
								title="Primary Modal"
								type={name}
								visible={typesVisible[name]}
								onClose={() => {
									this.setState({
										typesVisible: {
											...typesVisible,
											[name]: false,
										},
									});
								}}
							>
								Hello, Bamboo UI!~
							</Modal>
						</div>
					))}
				</div>

				<h2></h2>
				<p>
					
					<code>Modal.Preface</code>
				</p>

				<div className="preview">
					<Button
						onClick={() => {
							this.setState({ prefaceVisible: true });
						}}
					></Button>
					<Modal
						title="Preface Modal"
						visible={prefaceVisible}
						onClose={() => {
							this.setState({
								prefaceVisible: false,
							});
						}}
					>
						<Modal.Preface>
							<img src="./assets/light.min.jpg" alt="Light" />
						</Modal.Preface>
						<Modal.Content>
							<p>This is a Modal with Preface:</p>
							<pre className="code">
								{`
<Modal ...>
<Modal.Preface>
  <img src="/assets/light.jpg" />
</Modal.Preface>
...
</Modal>
`.trim()}
							</pre>
						</Modal.Content>
					</Modal>
				</div>

				<h2></h2>
				<p>
					<code>onConfirm</code>
					<code>noMaskClose</code>
				</p>

				<div className="measurement">
					<div className="preview">
						<Button
							onClick={() => {
								this.setState({ confirmVisible: true });
							}}
						></Button>
						<Modal
							title="Confirm Modal"
							visible={confirmVisible}
							onClose={() => {
								this.setState({
									confirmVisible: false,
								});
							}}
							onConfirm={() => {
								// TODO: do alert
								console.log('confirm');
								this.setState({
									confirmVisible: false,
								});
							}}
							noMaskClose
						>
							Click confirm will popup another modal
						</Modal>
					</div>

					<pre className="code">
						{`
<Modal onConfirm={...} noMaskClose ...>
...
</Modal>
`.trim()}
					</pre>
				</div>
				<p>
					<code>cancelText</code><code>confirmText</code>
					CancelConfirm
				</p>

				<h2></h2>
				<p>
					<code>loading</code>
				</p>

				<div className="measurement">
					<div className="preview">
						<Button
							onClick={() => {
								this.setState({ loadingVisible: true });
							}}
						></Button>
						<Modal
							title="Confirm Modal"
							visible={loadingVisible}
							loading={loading}
							onClose={() => {
								this.setState({
									loadingVisible: false,
								});
							}}
							onConfirm={() => {
								this.setState({ loading: true });
								setTimeout(() => {
									this.setState({ loading: false, loadingVisible: false });
								}, 2000);
							}}
						>
							Click confirm will display loading animation
						</Modal>
					</div>

					<pre className="code">
						{`
<Modal onConfirm={...} loading ...>
...
</Modal>
`.trim()}
					</pre>
				</div>

				<p>
					<code>lock</code><code>Modal.Footer</code>
				</p>
				<div className="preview">
					<Button
						onClick={() => {
							this.setState({ lockVisible: true });
						}}
					></Button>
					<Modal
						title="Confirm Modal"
						visible={lockVisible}
						lock={lock}
						onClose={() => {
							this.setState({
								lockVisible: false,
							});
						}}
						onConfirm={() => {
							this.setState({ lock: true });
							setTimeout(() => {
								this.setState({ lock: false });
							}, 2000);
						}}
					>
						Click confirm will lock dialog in 2 seconds.
					</Modal>
				</div>

				<h2>js</h2>
				<p>
					<code>alert</code>
					<code>onClose</code><code>onConfirm</code>
					promiseModalpromise
				</p>
				<div className="measurement">
					<div className="preview">
						<Button
							onClick={() => {
								Modal.showDialog({
									title: 'Hello',
									content: 'Bamboo UI',
								});
							}}
						></Button>

						{' '}

						<Button
							onClick={() => {
								Modal.showDialog({
									title: 'Hello',
									content: 'Bamboo UI',
									onConfirm: () => (
										new Promise((resolve) => {
											Modal.showDialog({
												title: null,
												content: 'You click confirm!',
												size: 'xs',
												onClose: resolve,
											});
										})
									),
								});
							}}
						></Button>
					</div>

					<pre className="code">
						{`
Modal.showDialog({
title: 'Hello',
content: 'Bamboo UI',
onConfirm: () => (
  new Promise((resolve) => {
	 Modal.showDialog({
		title: null,
		content: 'You click confirm!',
		size: 'xs',
		onClose: resolve,
	 });
  })
),
});
`.trim()}
					</pre>
				</div>

				<h2>
					<code>Title</code><code>Content</code><code>Footer</code>
				</h2>
				<p>
					Modal <code>title</code><code>content</code>children  <code>footer</code>
					   
					
				</p>

				<pre className="code">
					{`
<Modal>
<Modal.Title>Your title</Modal.Title>
<Modal.Content>Your content</Modal.Content>
<Modal.Footer>Your footer</Modal.Footer>
</Modal>
`.trim()}
				</pre>
			</div>
		);
	}
}

GroupPage.propTypes = {
	history: PropTypes.object,
};

export default GroupPage;



